.find {
  padding: 15px;
  height: 100%;
  max-height: 100dvh;
  display: grid;
  grid-template-columns: 1fr;
  place-items: center;

  &__container {
    position: relative;
    width: 100%;
    max-width: 480px;
    height: 100%;
    max-height: min(740px, 90dvh);
    border: 1px solid var(--bd-plans-separator);
    border-radius: 10px;
    overflow: hidden;
  }

  &__swiper {
    --swiper-pagination-color: var(--plan-color, var(--olive-dark-color));
    --swiper-pagination-bullet-width: 140px;
    --swiper-pagination-bullet-height: 6px;
    --swiper-pagination-bullet-inactive-color: var(--light-color);
    --swiper-pagination-bullet-inactive-opacity: 1;
    --swiper-pagination-bullet-horizontal-gap: 5px;
    --swiper-pagination-bullet-border-radius: 10px;

    width: 100%;
    height: 100%;

    & .find__swiper-pagination {
      height: max-content;
      position: absolute;
      top: 13px;
      display: flex;
      justify-content: center;
      z-index: 50;
    }

    .swiper-slide {
      position: relative;
    }
  }

  &__photo {
    height: 100%;
    user-select: none;
  }

  &__info {
    width: 100%;
    position: absolute;
    bottom: 0;
    display: grid;
    place-items: center;
    color: var(--light-color);
    z-index: 50;

    background-image: linear-gradient(
      180deg,
      hsl(0deg 0% 0% / 0%) 0%,
      hsl(0deg 0% 0% / 10%) 8%,
      hsl(0deg 0% 6% / 97%) 50%,
      hsl(0deg 0% 0% / 100%) 100%
    );
  }

  &__title {
    font-family: var(--font-nebulous);
    font-weight: 400;
    font-size: var(--fs-app-match-title);
  }

  &__description {
    max-width: 361px;
    margin-bottom: 31px;
    font-weight: 400;
    font-size: var(--fs-app-text);
    line-height: 1.7;
    text-align: justify;
    text-align-last: center;
  }

  &__controls {

    display: flex;
    justify-content: center;
    align-items: center;
    gap: 26px;

    &-btn {
      position: relative;
      z-index: 100;
      cursor: pointer;
      user-select: none;
    }
  }

  &__svg-nav {
    width: 52px;
    height: 52px;
    stroke: var(--light-color);
    stroke-width: 2px;
    fill: none;
  }

  &__swiper-button-prev,
  &__swiper-button-next {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 100;
    cursor: pointer;
  }

  &__swiper-button-prev {
    left: 20px;
  }

  &__swiper-button-next {
    right: 20px;
  }
}
